import React from 'react';
import {
  Route,
  Redirect,
  Switch,
  HashRouter,
} from 'react-router-dom';
import routes from './routes';

const renderRoutes = () => routes.map((ele) => (
  <Route
    exact={ele.exact}
    key={ele.id}
    path={ele.path}
    component={ele.component}
  />
));

/**
 *
 * 被<Route/>包裹的直接组件具有history、location、match的props
 * - 通过props传递给子组件
 * - 通过useHistory、useLocation、useRouteMatch可以得到
 */
const Router = function () {
  return (
    <HashRouter>
      <Switch>
        {renderRoutes()}
        {/** 用于找不到路由时的重定向，要和route为兄弟节点 */}
        <Redirect from='/*' to='/' />
      </Switch>
    </HashRouter>
  );
};
export default Router;

